/* App.css */

/* Estilos para dispositivos de escritorio */
@media only screen and (min-width: 768px) {
    .container {
      padding: 20px; /* Ajusta el espaciado */
    }
  
    .search-bar {
      width: 50%;
      margin-bottom: 40px;
    }
  
    .news-container {
      padding: 0 20px; /* Ajusta el espaciado de los lados */
    }
  
    .news-card {
      width: calc(25% - 15px); /* Ajusta el ancho de los cuadros de noticias */
      margin-bottom: 30px; /* Ajusta el margen inferior */
    }
  }
  
  /* Estilos para tablet */
  @media only screen and (max-width: 767px) {
    .search-bar {
      width: 50%;
      margin-bottom: 20px;
    }
  
    .news-container {
      padding: 40 40px; /* Ajusta el espaciado de los lados */
    }
  
    .news-card {
      width: calc(30% - 0px); /* Ajusta el ancho de los cuadros de noticias */
      margin-bottom: 20px; /* Ajusta el margen inferior */
    }
  }
  
  /* Estilos para smartphones */
  @media only screen and (max-width: 480px) {
    .search-bar {
      width: 70%;
      margin-bottom: 10px;
    }
  
    .news-container {
      padding: 0 5px; /* Ajusta el espaciado de los lados */
    }
  
    .news-card {
      width: 100%; /* Cuadro de noticias ocupa todo el ancho */
      margin-bottom: 10px; /* Ajusta el margen inferior */
    }
  }
  